        filteredTable: [],
        hover: {
          element: true,
          flower: false,
          wind: false,
          snow: false,
          cloud: false,
          moon: false,
          cosmos: false,
          dream: false,
          school: true,
          seisho: false,
          rinmeikan: false,
          frontier: false,
          seekfelt: false
