          { text: 'Name', value: 'subname', align: 'left', width: "15%"},
          { text: 'School', value: 'school', width: "1%"},
          { text: 'Element', value: 'element', width: "1%"},
          { text: 'Type', value: 'atktype', width: "1%"},
          { text: 'Position', value: 'position', width: "1%"},
          { text: 'Overall', value: 'overall', width: "1%"},
          { text: 'HP', value: 'hp', width: "1%"},
          { text: 'ATK', value: 'atk', width: "1%"},
          { text: 'DEF', value: 'def', width: "1%"},
          { text: 'MDEF', value: 'mdef', width: "1%"},
          { text: 'SPD', value: 'spd', width: "1%"},
