      },
      nameToID: function(input) {
        return result;
      }
    },
    computed: {
      filterCharadata: function() {
        this.filteredTable = this.intersection( this.filterAtktype(), this.filterElement(), this.filterPosition(), this.filterRarity(), this.filterSchool(), this.filterByName());
        if (this.searchFilter != "") {
          this.filteredTable = this.filteredBySearch();
        }
        if (this.checkAllFiltersOff()) {
          this.filteredTable = this.charadata;
        }
      }
    },
    created(){
      this.filteredTable = this.charadata;
      this.IDtoName();
    }
  }
